
.card{position:relative;margin-bottom:.75rem;background-color:#fff;border-radius:.25rem;border:1px solid rgba(0,0,0,0.125)
}
.card b{background:#ddd;margin-top:4px;display:inline-block;width:20%;height:1px;_overflow:hidden;vertical-align:middle
}
.card .card-block{padding:2.75rem
}
.card .card-text{padding-left:.25rem;color:#4b4b4b;font-weight:normal
}
.card .card-subtitle{padding-bottom:.25rem;color:#2f2a2a
}
.card .card-title{padding-bottom:1rem
}
.card .card-title span{display:inline-block;width:60%;color:#2f2a2a;font-weight:bold;vertical-align:middle;text-align:center
}
.card .card-action{margin-top:1.5rem;width:100%
}
.card .card-action .card-btn{display:block;margin:0 auto
}


